.scrollToTop {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
}

.button {
  background-color: #a0a0a071;
  color: white;
  border: none;
  border-radius: 50%;
  padding: 20px 25px;
  cursor: pointer;
  font-size: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.button:hover {
  background-color: #015f73;
  transform: scale(1.1);
}