.aboutSection {
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 1200px;
  margin: 0 auto;
}

.aboutSection h2 {
  font-size: 20pt;
  color: #01708c;
  margin-bottom: 20px;
}

.aboutSection p, .aboutSection ul {
  font-size: 14pt;
  color: #555;
  line-height: 1.5;
  margin-bottom: 10px;
}

.aboutSection img {
  max-width: 100%;
  height: auto;
}

#intro {
  text-align: justify;
}
