/* MainContent.module.css */
.banner{
  color: #e6e6e6;
  padding-top: 2rem;
}

.slogan{
  color: white;
  font-size: 20px;
}

.backgroundSection {
  background-image: url('../../img/Main-Background.jpg');
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  background-position-x: 45%;
  padding-bottom: 50vh; /* Set the padding-bottom to half of the viewport height */
  z-index: 100;
}

.blurredBackground {
  filter: opacity(0.8) brightness(1.05);
  transition: filter 1s ease-in-out, brightness 1s ease-in-out;
}

.mainContent {
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 100%;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.5); /* White background with 50% transparency */
}

.section {
  margin-bottom: 30px;
  width: 100%;
  box-sizing: border-box;
  position: relative;
}


#home {
  height: 50vh; /* Set the height to half of the viewport height */
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

h1, h2 {
  color: #333;
}

h1 {
  font-size: 36px;
}

h2 {
  font-size: 28px;
  color: #00b3b9;
  margin-bottom: 10px;
}

p {
  font-size: 16px;
  line-height: 1.5;
  color: #555;
}

ul {
  list-style-type: none;
}

.cardContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  width: 100%;
}

.card {
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 350px;
  text-align: center;
  transition: transform 0.3s ease;
  text-decoration: none;
}

.card:hover {
  transform: scale(1.05); /* Enlarge the card on hover */
}

.cardImage {
  width: 100%;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.card h3 {
  font-size: 24px;
  color: #01708c;
  margin-top: 10px;
  margin-bottom: 10px;
}

.card p {
  font-size: 16px;
  color: #555;
}

.contactForm {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  max-width: 400px;
  margin: 0 auto; /* Center the form horizontally */

}

.formGroup {
  display: flex;
  flex-direction: column;
}

.formGroup label {
  margin-bottom: 5px;
  font-weight: bold;
  color:#008f0c
}

.formGroup input,
.formGroup textarea {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.submitButton {
  padding: 10px 20px;
  background-color: #00a50e;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.submitButton:hover {
  background-color: #008f0c;
}

@media (min-width: 600px) {
  .cardContainer {
    flex-direction: row; /* Arrange cards horizontally on larger screens */
    flex-wrap: wrap;
  }
}

@media (max-width: 599px) {
  .card {
    max-width: 90%; /* Ensure cards fit into the screen on mobile devices */
  }
}

.navLink {
  font-size: 16pt;
  color: #c0c0c0;
  text-decoration: underline;
}