.footer {
  background-color: #f1f1f1;
  padding: 20px;
  text-align: center;
  font-size: 14px;
  color: #333;
}

.footer p {
  margin: 0;
}

.footer a {
  color: #0275d8;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}

.footer .social-links {
  margin-top: 10px;
}

.footer .social-links a {
  margin: 0 10px;
  color: #333;
}

.footer .social-links a:hover {
  color: #0275d8;
}

.socialLink{
  margin: 0 10px;
  color: #333;
}