.blogContainer {
  padding: 20px;
}

.title{
  padding-top: 3rem;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.loadMoreButton {
  margin: 20px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 5px;
}

.loadMoreButton:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}