.latestWPPost {
  background-color: rgba(255, 232, 232, 0.678);
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 20px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
  overflow-wrap: break-word;
  max-width: 100%;
  max-height: 600px;
  position: relative;
}

.latestWPPost::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50%;
  background: linear-gradient(to bottom, rgba(255, 232, 232, 0) 0%, rgba(255, 232, 232, 0.678) 100%);
  pointer-events: none;
  transition: opacity 1s ease;
  opacity: 1;
}

.latestWPPost.expanded::after {
  opacity: 0;
}

.latestWPPost.expanded {
  max-height: none;
  background-color: rgba(252, 246, 246, 0.678);
}

.latestWPPost h2 {
  font-size: 20pt;
  color: #01708c;
  margin-top: 10px;
  margin-bottom: 10px;
}

.latestWPPost img {
  max-width: 100%; /* Ensure images do not overflow */
  height: auto; /* Maintain aspect ratio */
}

.toggleButton {
  background-color: #00b3b9;
  color: white;
  border: #01708c 1px solid;
  border-radius: 4px;
  padding: 10px 20px;
  cursor: pointer;
  margin-bottom: 10px;
  transition: background-color 0.3s ease, transform 0.3s ease;
  font-size: 18pt;
}

.toggleButton:hover {
  background-color: #015f73;
  transform: scale(1.05);
}