.postCard {
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    cursor: pointer;
    transition: transform 0.3s ease;
    display: flex;
    align-items: center;
    max-width: 90%;
    margin: 20px auto;
    word-wrap: break-word;
    overflow-wrap: break-word;
    text-overflow: ellipsis;
    overflow: hidden;
}

.postDate {
    font-size: 10pt;
    color: #949494;
    margin-bottom: 50px;
}

.postTitle {
    font-size: 22pt;
    color: #01708c;
    margin-top: 10px;
    margin-bottom: 10px;
    text-align: left;
}

.postExcerpt {
    font-size: 16pt;
    color: #555;
    text-align: justify;
    margin-bottom: 10px;
}

.postCard:hover {
    transform: scale(1.05);
}

.cardhint{
    color: #949494;
    font-size: 10pt;
    margin-top: 20px
}

.postImage {
    max-width: 10%;
    max-height: 10%;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    margin-right: 20px;
    margin-top: 10px
}

.textContent {
    flex: 1;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9000;
}

.overlayContent {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    max-width: 100%;
    max-height: 100%;
    overflow-y: auto;
    position: relative;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    max-height: 98vh;
}

.overlayContent img {
    max-width: 100%;
    height: auto;
}

.closeButton {
    width: 2em;
    position: fixed;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    background: rgba(255, 255, 255, 0.151);
    border: none;
    font-size: 28px;
    cursor: pointer;
    color: #aaaaaa;
    z-index: 9001; /* Ensure it stays above other content */
}
.closeButton:hover {
    color: #ebebeb;
}

@media (max-width: 768px) {
    .closeButton {
        width: 3em; 
        position: fixed;
        top: 10px;
        left: 50%;
        transform: translateX(-50%);
        background: rgba(255, 255, 255, 0.219);
        border: none;
        font-size: 32px;
        cursor: pointer;
        color: #cccccc;
        z-index: 9001; /* Ensure it stays above other content */
    }
}